
import CheckIcon from 'vux/src/components/check-icon/index.vue'

export default {
name: 'privateMsg',
components: {
CheckIcon
},
data() {
return {
yuan1: true,
hongBaoList: [{
yuan: 1,
isChecked: true
},
{
yuan: 5,
isChecked: false
},
{
yuan: 10,
isChecked: false
},
{
yuan: 20,
isChecked: false
},
{
yuan: 50,
isChecked: false
},
{
yuan: 99,
isChecked: false
}]
}
},
methods: {
chooseItem(index) {
for(var  i in this.hongBaoList){
this.hongBaoList[i].isChecked=false;
}
this.hongBaoList[index].isChecked = true;
}
}
}



