

import Vue from 'vue'
import App from './App'
import router from './router'


import store from './store'

import '@/assets/css/main.css'
import '@/assets/css/iconfont.css'

import '@/assets/js/ydui.flexible'


import {http,checkTel} from '@/assets/js/common.js'

Vue.prototype.$http = http;
Vue.prototype.checkTel=checkTel


Vue.config.productionTip = false

/* eslint-disable no-new */





import Datetime from 'vux/src/components/datetime/index.vue'
import Group from 'vux/src/components/group/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Range from 'vux/src/components/range/index.vue'

Vue.component(Datetime.name, Datetime, Range, Group.name, Group);

Vue.component('popup-picker', PopupPicker)




Vue.directive('transfer-dom', TransferDom)







new Vue({
el: '#app',
router,
store,
components: { App },
template: '<App/>',
render: h => h(App)
})



