

import Vue from 'vue'




import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'

export default {
name: 'appFooter',
data() {
return {
isActive: true,
routers: [
{
name: '找作者',
iconfontName: 'iconfont icon-search-1-copy',
link: '/',
active: true
}, {
name: '任务',
iconfontName: 'iconfont icon-iconset0137',
link: 'mission',
active: false
}, {
name: '圈子',
iconfontName: 'iconfont icon-zhinanzhen',
link: 'circle',
active: false
}, {
name: '我的',
iconfontName: 'iconfont icon-wode',
link: 'my',
active: false
},
]
}
},
components: {
Tabbar,
TabbarItem,
Group,
Cell
},
watch: {
$route: {
handler: function(val, oldVal) {
console.log(val);
for (var i in routers) {
if (routers[i].path == val.path) {
routers[i].active = !routers[i].active;
}
}
},

deep: true
}

},
methods: {
tabActive() {
console.log("点击")

}
}
}



