export default {
  name:"choose_identity",
  data(){
      return {
        isActive:true,
      }
  },
  methods:{
      chooseWriter(){
          this.isActive=!this.isActive;
      },
      gotoFillIn(){
          this.$router.push({
              path:'fill_in_information'
          })
      }
  }
}



